<template>
    <div>
        <app-portfolio :portfolios="portfolios"></app-portfolio>
    </div>
</template>

<script>
    import appPortfolio from '../../components/category/Portfolio'
    export default {
        components: {
          appPortfolio
        },
        data: () => ({
            portfolios: []
        }),
        methods: {
          async filterCategory () {
                const results = await this.$axios.get('category/filtercategory',
                    { params: { categoryId: this.$route.params.id } })
              this.portfolios = results.data.portfolios
            }
        },
        created () {
            this.filterCategory()
        }
    }
</script>
